<template>
  <div class="page-weixin-content page-detail">
    <h-topbar title="微信文稿详情"></h-topbar>
    <div
      class="page-weixin-content-main page-detail-main"
      :style="{'bottom':operate_able.length ? '70px' : '0'}"
    >
      <h-tab :navItems="navItems" :tabKey="tabKey" @switch="onSwitch">
        <div ref="message">
          <list-message :content="content" type="weixin" v-if="content"></list-message>
        </div>
        <div ref="comments">
          <list-comments
            :mediumId="content_id"
            :accountId="account_id"
            :isComment="isComment"
            :isAudit="isAudit"
            type="weixin"
          ></list-comments>
        </div>
        <div ref="records">
          <list-operlog
            :mediumId="content_id"
            :accountId="account_id"
            :isComment="isComment"
            :isAudit="isAudit"
            type="weixin"
          ></list-operlog>
        </div>
      </h-tab>
    </div>
    <footer class="s-footer" v-if="operate_able.length">
      <span
        :class="`s-${item.key}`"
        v-for="item in operate_able"
        :key="item.key"
        @click="handle(item.key)"
      >{{item.name}}</span>
    </footer>
    <!-- 审核 -->
    <mt-popup class="mint-popup-audit" v-model="isAudit" position="bottom">
      <footer>
        <input placeholder="请输入审核意见" v-model="comment" />
        <button class="s-fail" @click="pass(false)">退回</button>
        <button class="s-success" @click="pass(true)">通过</button>
      </footer>
    </mt-popup>
    <!-- 批注 -->
    <mt-popup class="mint-popup-comment" v-model="isComment" position="bottom">
      <footer>
        <input placeholder="我想说..." v-model.trim="text" />
        <button class="s-success" @click="comments">发表</button>
      </footer>
    </mt-popup>
    <!-- 删除 -->
    <mt-popup class="mint-popup-delete" v-model="isDelete" position="center">
      <div class="s-de">
        <p>提示</p>
        <p class="s-de-info">是否确定删除此图文？</p>
        <div>
          <button class="s-de-cancel" @click="cancel">取消</button>
          <button class="s-de-confirm" @click="del">删除</button>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      content: '',
      account_name: '',
      account_id: undefined,
      content_id: undefined,
      tabKey: '',
      navItems: [
        { name: '基本信息', key: 'message' },
        { name: '批注', key: 'comments' },
        { name: '操作纪录', key: 'records' }
      ],
      operate_able: [],
      isAudit: false,
      isComment: false,
      isDelete: false,
      text: '',
      comment: '',
      operate_status: -1
    };
  },
  created() {
    let { params } = this.$route;
    let { account_id, content_id } = params;
    this.account_id = account_id;
    this.content_id = content_id;
    this.tabKey = this.navItems[0].key || '';
    this.$Model.Weixin.accounts().then(data => {
      let account = data.find(item => item.id == account_id);
      this.account_name = account.name;
    });
    this.init();
  },
  methods: {
    init() {
      this.$Model.Weixin.content(this.account_id, this.content_id).then(data => {
        this.content = data;
        this.content.accountName = this.account_name;
        let { operateAble } = data;
        this.operate_able = [];
        // 1-修改，2-审核，3-删除，4-恢复，5-撤销，6-批注，7-预览，8-群发, 9-提交
        operateAble.forEach((item, index) => {
          if (operateAble[index] == 2) {
            this.operate_able.push({ name: '审核', key: 'audit_' });
          } else if (operateAble[index] == 3) {
            this.operate_able.push({ name: '删除', key: 'delete' });
          } else if (operateAble[index] == 5) {
            this.operate_able.push({ name: '撤销', key: 'revoke' });
          } else if (operateAble[index] == 6) {
            this.operate_able.push({ name: '批注', key: 'comment' });
          } else if (operateAble[index] == 8) {
            this.operate_able.push({ name: '发布', key: 'publish' });
          } else if (operateAble[index] == 9) {
            this.operate_able.push({ name: '提交', key: 'accept' });
          }
        });
        // 获取可撤销/退回状态
        this.$Model.Weixin.status(this.account_id, this.content_id).then(data => {
          if (data.length) {
            this.operate_status = data[0].status;
          }
        });
      });
    },
    onSwitch(key) {
      this.tabKey = key;
      this.$nextTick(() => {
        this.$refs[key].scrollIntoView();
      });
    },
    handle(key) {
      if (key == 'audit_') {
        this.isAudit = true;
      } else if (key == 'delete') {
        this.isDelete = true;
      } else if (key == 'revoke') {
        this.revoke();
      } else if (key == 'comment') {
        this.isComment = true;
      } else if (key == 'publish') {
        this.publish();
      } else if (key == 'accept') {
        this.commit();
      }
    },
    pass(is_approved) {
      // 审核true/false
      let data = {
        is_approved: is_approved,
        comment: this.comment,
        status: this.operate_status
      };
      this.$Model.Weixin.audit(this.account_id, this.content_id, data).then(() => {
        this.isAudit = false;
        this.comment = '';
        this.init();
      });
    },
    revoke() {
      // 撤销
      this.$Model.Weixin.revoke(this.account_id, this.content_id, this.operate_status).then(() => {
        this.init();
        this.$toast('撤销成功');
      });
    },
    del() {
      // 删除
      this.$Model.Weixin.delete(this.account_id, this.content_id).then(() => {
        this.$router.back();
      });
    },
    cancel() {
      this.isDelete = false;
    },
    comments() {
      // 批注
      if (this.text != '') {
        this.$Model.Weixin.addComments(this.account_id, this.content_id, JSON.stringify(this.text)).then(data => {
          this.isComment = false;
          this.text = '';
        });
      } else {
        this.$toast('批注不能为空');
      }
    },
    publish() {
      let { account_id, content_id } = this;
      this.$router.push(`/weixin/${account_id}/content/${content_id}/publish/now`);
    },
    commit() {
      // 提交群发图文
      let { account_id, content_id } = this;
      this.$Model.Weixin.commit(account_id, content_id).then(() => {
        this.$toast('提交成功');
        this.init();
      });
    }
  },
  beforeRouteLeave(to, from, next) {
    if (to.name == '微信搜索') {
      to.meta.isKeepLive = true;
    }
    next();
  }
};
</script>

<style lang="scss">
.page-weixin-content {
  .s-footer {
    @each $img in audit_, revoke, delete, comment, publish, accept {
      span.s-#{$img} {
        background-image: url('../images/detail/#{$img}@2x.png');
      }
    }
  }
}
</style>
